.input {
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: column;

  width: 50%;
  margin-top: 40px;
}

.buttons {
  margin-top: 25px;
}

input {
  border-radius: 5px;
  padding: 10px;
  outline: none;
  border: none;
  margin-bottom: 20px;
  font-size: 20px;
}

input:last-of-type {
  margin: 0;
}

img {
  width: 1em;
  height: 1em;
  padding-top: 5px;
}

button {
  font-size: 2.25em;
  color: aliceblue;
  text-align: center;
  padding: 12px 0;
  border-radius: 5px;
  outline: none;
  border: none;
}

.btn {
  width: 47.5%;
  justify-content: space-between;
}

.btn.print {
  background-color: rgb(197, 96, 180);
  float: left;
}

.btn.copy {
  background-color: rgba(71, 111, 253, 0.53);
  float: right;
}